import Blog from "../Blog.vue";
export default {
    components: { Blog },
    data() {
        return {
            isplay: true,
            time: "2018-9-15",
            items: [
                {
                    title: "解释",
                    target: "#Explain"
                },
                { divider: true },
                {
                    title: "效果",
                    target: "#Result1"
                },
                { divider: true },
                {
                    title: "代码",
                    target: "#Code1"
                }
