            html: `
            <div class="animatebox">
                <div class="man">
                    <div class="head">
                        <div></div>
                        <div></div>
                        <div>
                            <div class="mouth"><div></div></div>
                        </div>
                        <div></div>
                        <div></div>
                        <div></div>
                    </div>
                    <div class="neck"></div>
                    <div class="body">
                        <div>
                            <div class="hand"></div>
                        </div>
                        <div>
                            <div class="hand"></div>
                        </div>
                        <div></div>
                        <div></div>
                        <div></div>
                        <div>
                            <div class="foot foot1"></div>
                            <div class="foot foot2"></div>
                        </div>
                    </div>
                </div>
                <div class="road"></div>
            </div>
            `,
            js: `
            isplay = !isplay;
            if(isplay){
                document.getElementsByClassName("man")[0].style.animationPlayState='running';
                document.getElementsByClassName("head")[0].style.animationPlayState='running';
                document.getElementsByClassName("hand")[0].style.animationPlayState='running';
                document.getElementsByClassName("hand")[1].style.animationPlayState='running';
                document.getElementsByClassName("foot1")[0].style.animationPlayState='running';
                document.getElementsByClassName("foot2")[0].style.animationPlayState='running';
            }else{
                document.getElementsByClassName("man")[0].style.animationPlayState='paused';
                document.getElementsByClassName("head")[0].style.animationPlayState='paused';
                document.getElementsByClassName("hand")[0].style.animationPlayState='paused';
                document.getElementsByClassName("hand")[1].style.animationPlayState='paused';
                document.getElementsByClassName("foot1")[0].style.animationPlayState='paused';
                document.getElementsByClassName("foot2")[0].style.animationPlayState='paused';
            }
            `,
            css: `
            .animatebox{
                position: relative;
                height: 500px;
                background-color: antiquewhite;
            }

            .animatebox .road {
                position: absolute;
                top: 280px;
                left: 0;
                width: 100%;
                height: 100px;
                background-image: url(../../assets/road.png);
                background-size: 64px;
                background-repeat: repeat;
            }

            .animatebox .man {
                animation-name: man;
                animation-duration: 12s;
                animation-timing-function: linear;
                animation-iteration-count: infinite;
                width: 200px;
                height: 300px;
                position: absolute;
                z-index: 1;
            }

            .animatebox .neck{ 
                z-index: 1;
                position:absolute;
                top: 125px;
                left: 15px;
                height: 10px; 
                width: 50px;
                transform: translateZ(-50px);
                background:violet;
            }

            .animatebox .neck:before{
                position: absolute; 
                top: -5px; 
                content: ""; 
                display: block; 
                width: 50px; 
                height: 10px; 
                border-radius:50%; 
                background:violet;
            }

            .animatebox .neck:after{
                position: absolute;
                bottom: -5px;
                content: "";
                display: block;
                width: 50px;
                height: 10px;
                border-radius:50%;
                background:violet;
            }

            .animatebox .head{
                z-index: 2;
                position:absolute;
                top: 20px;
                left: 0;
                height: 100px; 
                width: 100px; 
                transform-style: preserve-3d;
                perspective: 700px;
                perspective-origin: 200% -100%;

                animation-name: head;
                animation-duration: 3s;
                animation-timing-function: linear;
                animation-iteration-count: infinite;
                animation-direction: alternate;
            }

            .animatebox .head > div{
                position:absolute;
                top: 0;
                left: 0;
                height: 100px; 
                width: 100px;
                border: 1px solid #ccc;
            }

            .animatebox .head > div:nth-of-type(1) {
                transform: translateZ(50px);
            }

            .animatebox .head > div:nth-of-type(2) {
                transform: rotateY(180deg) translateZ(50px);
            }

            .animatebox .head > div:nth-of-type(3) {
                transform: rotateY(90deg) translateZ(50px);
            }

            .animatebox .head > div:nth-of-type(3)::before{
                content: '·';
                position: absolute;
                top: 10px;
                left: 20px;
                color: blue;
                font-size: 35px;
            }

            .animatebox .head > div:nth-of-type(3) .mouth {
                position: absolute;
                bottom: 0px;
                left: 0px;
                width: 100px;
                height: 50px;
                text-align: center;
                overflow: hidden;
            }

            .animatebox .head > div:nth-of-type(3) .mouth div{
                margin: 0 auto;
                position: absolute;
                top: -30px;
                left: 20px;
                width: 50px;
                height: 50px;
                border-radius:50%;
                border: 1px solid black;
            }

            .animatebox .head > div:nth-of-type(3)::after{
                content: '·';
                position: absolute;
                top: 10px;
                right: 30px;
                color: blue;
                font-size: 35px;
            }

            .animatebox .head > div:nth-of-type(4) {
                transform: rotateY(-90deg) translateZ(50px);
            }

            .animatebox .head > div:nth-of-type(5) {  
                transform: rotateX(90deg) translateZ(50px);
            }

            .animatebox .head > div:nth-of-type(6) {
                transform: rotateX(-90deg) translateZ(50px);
            }

            .animatebox .body {
                position:absolute;
                top: 140px;
                left: 13px;
                height: 100px; 
                width: 100px; 
                transform-style: preserve-3d;
                perspective: 1000px;
                perspective-origin: 200% -150%;
            }

            .animatebox .body > div{
                position:absolute;
                top: 0;
                left: 0;
                border: 1px solid #ccc;
            }

            .animatebox .body > div:nth-of-type(1) {
                transform: translateZ(50px);
                width: 50px;
                height: 100px;
            }

            .animatebox .body > div:nth-of-type(2) {
                transform: rotateY(180deg) translateZ(50px);
                width: 50px;
                height: 100px;
            }

            .animatebox .body > div:nth-of-type(3) {
                transform: rotateY(90deg) translateZ(0px);
                width: 100px;
                height: 100px;
            }

            .animatebox .body > div:nth-of-type(4) {
                transform: rotateY(-90deg) translateZ(50px);
                width: 100px;
                height: 100px;
            }

            .animatebox .body > div:nth-of-type(5) {  
                transform: rotateX(90deg) translateZ(50px);
                width: 50px;
                height: 100px;
            }

            .animatebox .body > div:nth-of-type(6) {
                transform: rotateX(-90deg) translateZ(50px);
                width: 50px;
                height: 100px;
                transform-style: preserve-3d;
                perspective: 1000px;
                perspective-origin: 150% -150%;
            }

            .animatebox .body .hand {
                position: absolute;
                top: 50px;
                left: 15px;
                width: 20px;
                height: 100px;
                border-radius: 5px;
                z-index: 1;
                background-color:mediumslateblue;
                transform-origin: top;
                animation-name: hand;
                animation-duration: 1s;
                animation-timing-function: linear;
                animation-iteration-count: infinite;
                animation-direction: alternate;
            }

            .animatebox .body .hand::before{
                content: "·";
                position: absolute;
                left: 7px;
                top: 0;
                font-size: 15px;
                font-weight: bold;
                color: red;
            }

            .animatebox .body .foot{
                position: absolute;
                top: 50px;
                left: 0;
                width: 20px;
                height: 100px;
                border-radius: 5px;
                background-color: plum;
            }

            .animatebox .body .foot1 {

                transform-origin: bottom;
                animation-name: foot1;
                animation-duration: 1s;
                animation-timing-function: linear;
                animation-iteration-count: infinite;
                animation-direction: alternate;
            }

            .animatebox .body .foot2 {
                transform-origin: bottom;
                animation-name: foot2;
                animation-duration: 1s;
                animation-timing-function: linear;
                animation-iteration-count: infinite;
                animation-direction: alternate;
            }

            @keyframes man {
                0% { transform: translateX(0)}
                100% { transform: translateX(1000px)}
            }

            @keyframes head {
                0% { transform: rotateY(0);}
                100% { transform: rotateY(-30deg);}
            }

            @keyframes hand {
                0% { transform: rotateZ(60deg); }
                
                100% { transform: rotateZ(-60deg); }
            }

            @keyframes foot1 {
                0% { transform: rotateX(-90deg) translateY(-25px) rotateZ(40deg); }
                
                100% { transform: rotateX(-90deg) translateY(-25px) rotateZ(-40deg); }
            }

            @keyframes foot2 {
                0% { transform: rotateX(-90deg) translateY(-25px) translateZ(50px) rotateZ(-40deg); }
                
                100% { transform: rotateX(-90deg) translateY(-25px) translateZ(50px) rotateZ(40deg); }
            }
