    mounted() {},
    methods: {
        playandpause() {
            this.isplay = !this.isplay;
            this.$nextTick(() => {
                if(this.isplay){
                    document.getElementsByClassName("man")[0].style.animationPlayState='running';
                    document.getElementsByClassName("head")[0].style.animationPlayState='running';
                    document.getElementsByClassName("hand")[0].style.animationPlayState='running';
