                    document.getElementsByClassName("foot1")[0].style.animationPlayState='running';
                    document.getElementsByClassName("foot2")[0].style.animationPlayState='running';
                }else{
                    document.getElementsByClassName("man")[0].style.animationPlayState='paused';
                    document.getElementsByClassName("head")[0].style.animationPlayState='paused';
                    document.getElementsByClassName("hand")[0].style.animationPlayState='paused';
                    document.getElementsByClassName("hand")[1].style.animationPlayState='paused';
                    document.getElementsByClassName("foot1")[0].style.animationPlayState='paused';
                    document.getElementsByClassName("foot2")[0].style.animationPlayState='paused';
                }
            })
        }
    },
};
</script>

